  --black-3: #202124;
  --white: #fff;
  --white-2: #f6f8f9;
  --grey: #cbd0d3;
  --grey-2: #ebeded;
  --grey-3: #a6adb4;
  --blue: #00a2e2;
  --green: #209e86;
  --green-2: #1c3b3f;
  --red: #b20013;
  /* Typography */
  --main-font: 'Montserrat', sans-serif;
  /* Units */
  --container-xxl: 90rem;
  --container-xl: 71.25rem;
  --container-lg: 60rem;
  --container-md: 45rem;
  --container-sm: 33.75rem;
  --gutter: 0.9375rem; }

html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  color: var(--black);
  font-family: var(--main-font);
  font-size: 1rem; }

a {
  color: var(--black);
  text-decoration: none; }

img {
  display: block;
  max-width: 100%; }

button {
  cursor: pointer;
  display: block; }

.container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  width: 100%;
  padding-left: calc(var(--gutter) * 2);
  padding-right: calc(var(--gutter) * 2); }
  @media (min-width: 36em) {
