      line-height: 2.125rem;
      margin-bottom: 0.75rem;
      text-transform: uppercase; }
    .newsletter__content p {
      font-size: 0.875rem;
      line-height: 1.25rem; }
  .newsletter__form {
    display: grid;
    gap: 1.25rem;
    position: relative; }
    @media (min-width: 36em) {
      .newsletter__form {
        grid-template-columns: 1fr auto; } }
    @media (min-width: 62em) {
      .newsletter__form {
        grid-column: 7 / 12; } }
    .newsletter__form::after {
      content: '';
